<template>
	<el-container class="home-container">
		<!-- 头部区域 -->
	  <el-header>
			<div>
				<img src="../assets/myTouxiang.jpg" alt="" style="height: 50px; margin-left: 4px;">
				<span>后台管理系统</span>
			</div>
			<el-button type="info" @click="logout">退出登录</el-button>
		</el-header>
		<!-- 页面主体区域 -->
	  <el-container>
			<!-- 侧边栏 -->
	    <el-aside :width="isCollapse ? '64px': '200px'">
				<div class="toggle-button" @click="toggleCollpse">|||</div>
				<!-- 侧边栏菜单区域 -->
				<el-menu background-color="#333744" text-color="#fff" active-text-color="#409bff" 
				unique-opened :collapse="isCollapse" :collapse-transition="false" router
				:default-active="activePath">
					<!-- 一级菜单 -->
				      <el-submenu :index="item.id + ''" v-for="item in menulist" :key="item.id">
								<!-- 一级菜单模板区域 -->
				        <template slot="title">
									<!-- 图标 -->
				          <i :class="iconsObj[item.id]"></i> 
									<!-- 文本 -->
				          <span>{{item.authName}}</span>
				        </template>
								
								<!-- 二级菜单 -->
								<el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" 
								:key="subItem.id" @click="saveNavState('/' + subItem.path)">
									<template slot="title">
										<!-- 图标 -->
									  <i class="el-icon-menu"></i>
										<!-- 文本 -->
									  <span>{{subItem.authName}}</span>
									</template>
								</el-menu-item>
				      
				      </el-submenu>
				     
				    </el-menu>
			</el-aside>
			<!-- 右侧内容主体 -->
	    <el-main>
				<!-- 路由占位符 -->
				<router-view></router-view>
			</el-main>
			
	  </el-container>
	</el-container>
</template>

<script>
	export default {
		data(){
			return {
				menulist:[],
				iconsObj:{
					125: 'iconfont icon-users',
					103: 'iconfont icon-tijikongjian',
					101: 'iconfont icon-shangpin',
					102: 'iconfont icon-danju',
					145: 'iconfont icon-baobiao'
				},
				isCollapse:false,//是否折叠
				activePath:'',//保存侧边栏激活状态
			}
		},
		created(){
			this.getMenuList()
			this.activePath = window.sessionStorage.getItem('activePath')
		},
		methods:{
			logout(){
				//退出功能实现，清空token并跳转到登录页
				window.sessionStorage.clear();
				this.$router.push("/login");
			},
			//获取所有的菜单
			async getMenuList(){
				const {data:res} = await this.$http.get('menus')
				if(res.meta.status !== 200) return this.$message.error(res.meta.msg)
				this.menulist = res.data
				console.log(this.menulist)
			},
			//点击按钮，切换菜单的展开折叠
			toggleCollpse(){
				this.isCollapse = !this.isCollapse
			},
			//保存链接的激活状态
			saveNavState(activePath){
				window.sessionStorage.setItem('activePath',activePath)
				this.activePath = activePath
			}
		}
	}
</script>

<style lang="less" scoped>
	.home-container{
		height:100%;
	}
	.el-header {
	  background-color: #373d41;
		display: flex;
		justify-content: space-between;
		padding-left:0;
		align-items:center;
		color:#fff;
		font-size:20px;
		> div {
			display: flex;
			align-items: center;
			span {
				margin-left:15px;
			}
		}
	}
	  .el-footer{
			
	}
	  .el-aside {
	    background-color: #333744;
			.el-menu{
				border-right:none;
			}
	}
	  
	  .el-main {
	    background-color: #eaedf1;
	}
	.iconfont {
		margin-right:10px;
	}
	.toggle-button{
		background-color: #4a5064;
		font-size: 10px;
		color:#fff;
		line-height: 24px;
		text-align: center;
		letter-spacing: 0.2em;
		cursor:pointer;
	}
</style>
